.container_details {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-left: 5rem;
    margin-right: 5rem;
  }
  
  .image_details {
    width: 23rem;
    height: 23rem;
    background: solid black 3px;
    margin-bottom: 2rem;
    margin: 0 auto;
  }
  
  .image_details img {
    width: 20rem;
    height: 20rem;
    border-radius: 10px;
    border: solid rgba(0, 0, 0, 1) 3px;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 1);
  }
  
  .info_details {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 3rem;
    background: rgba(188, 108, 37, 0.1);
    box-shadow: 5px 7px 10px rgba(0, 0, 0, 1);
    padding: 6px 15px;
    border-radius: 5px;
  }
  
  .info_details h3 {
    font-weight: 800;
    font-size: 1.5rem;
    text-transform: uppercase;
    color: #000;
  }
  
  .info_details p {
    font-size: 15px;
    font-weight: 500;
    color: #fcf6bd;
  }
  
  .info_details li {
    font-size: 15px;
    font-weight: 500;
    color: #fcf6bd;
    text-decoration: none;
    list-style: none;
  }
  
  /* Media queries */
  @media (max-width: 768px) {
    .container_details {
      flex-direction: column;
      align-items: center;
      margin-left: 1rem;
      margin-right: 1rem;
    }
  
    .image_details {
      margin-bottom: 1rem;
      margin: 0 auto;
      padding-left: 2rem;
    }
  }